import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import {UniversityService} from '../university.service';
import {UserService} from '../user.service';
import {User1Service} from '../user1.service';
import {Librarian1, Reader2} from '../entity';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  private username: string;
  private password: string;
  private role: string;
  reader: Reader2;
  librarian: Librarian1;
  constructor(
    private router: Router,
    private loginService: UniversityService,
    private userService: UserService,
    private user1Service: User1Service
  ) { }

  ngOnInit() { // 组件被创建
  }
  // 从文本框中获取两个参数值，赋值给变量
  login(role: string, username: string, password: string): void { // 登录跳转页面
    this.role = role;
    this.username = username;
    this.password = password;
    this.loginService.checkUser({role: this.role, username: this.username, password: this.password})
      .subscribe((res) => {
        if (res.username) {
            // alert(this.role);
            if (this.role === '管理员') {
              window.localStorage.setItem('userName', res.librarian.name);
              this.librarian = {id: res.librarian.id};
              this.user1Service.setLibrarian(this.librarian);
              // alert(this.user1Service.getLibrarian().id);
              this.router.navigateByUrl('/index/book');
            } else if (this.role === '读者') {
              window.localStorage.setItem('userName', res.reader.name);
            // tslint:disable-next-line:max-line-length
              this.reader = {id: res.reader.id};
              this.userService.setReader(this.reader);
              // alert(this.userService.getReader().id);
              this.router.navigateByUrl('/index1/borrowing');
            }
        } else {
          alert(res.message);
        }
      });
  }
}
