// service类用来处理数据，获取数据，与后台进行交互 ng g service school
import { Injectable } from '@angular/core';
// 实现Http通信(get/post/put/delete)
import { HttpClient, HttpHeaders } from '@angular/common/http';
// 监听来自后台的数据
import { Observable, of } from 'rxjs';
// 异常处理
import { catchError, map, tap } from 'rxjs/operators';
// 引入实体类，使用实体类中定义的属性
// tslint:disable-next-line:max-line-length
import {Librarian, Response, User} from './entity';
// 中间传输的选项数据（http传输，json格式）
const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};
// 注入依赖 （其他组件可调用）
@Injectable({
  providedIn: 'root'
})
export class LibrarianService {
  private librarianUrl = 'http://localhost:8080/librarian.ctl';
  private userUrl = 'http://localhost:8080/user.ctl';
  // 接口，获取后端所提供的数据
  constructor(private http: HttpClient) { }
  // College类型的json数组（school组件调用）
  /** GET college by id. Will 404 if id not found */
  getLibrarian(id: number): Observable<Librarian> {
    const url = this.librarianUrl + '?id=' + id;
    return this.http.get<Librarian>(url).pipe(
      catchError(this.handleError<Librarian>(`getLibrarian id=${id}`))
    );
  }
  getUser(id: number): Observable<User> {
    const url = this.userUrl + '?role=librarian&id=' + id;
    return this.http.get<User>(url).pipe(
      catchError(this.handleError<User>(`getUser id=${id}`))
    );
  }
  changePassword(user: {id: number, password: string, role: string}): Observable<Response> {
    return this.http.put<Response>(this.userUrl, user, httpOptions).pipe(
      catchError(this.handleError<Response>('changePassword'))
    );
  }
  /**
   * Handle Http operation that failed.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.error(error); // log to console instead
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
