import { Component, OnInit, Input} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import {BookService} from '../book.service';
import {Book} from '../entity';

@Component({
  selector: 'app-add-book',
  templateUrl: './add-book.component.html',
  styleUrls: ['./add-book.component.css']
})
export class AddBookComponent implements OnInit {
  @Input() book: Book;
  constructor(
    private route: ActivatedRoute,
    private bookService: BookService,
    private location: Location
  ) { }

  ngOnInit() {
  }
  // tslint:disable-next-line:variable-name
  save(no: string, name: string, writer: string, press: string, address: string, total: string, borrowedNumber: string): void { // 多个文本框数据
    // tslint:disable-next-line:max-line-length
    this.book = {id: null, no: no.trim(), name: name.trim(), writer: writer.trim(), press: press.trim(), address: address.trim(), total: total.trim(), borrowedNumber: borrowedNumber.trim()};
    // tslint:disable-next-line:max-line-length
    if (no.length === 0 || name.length === 0 || writer.length === 0 || press.length === 0 || address.length === 0 || total.length === 0 || borrowedNumber.length === 0) {
      alert('请检查输入信息是否完整！');
    }
    if (!name) { return; }
    this.bookService.addBook(this.book)
      .subscribe((res) => {alert(res.message); this.goBack(); });
  }
  goBack(): void {
    this.location.back();
  }
}
