import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { User } from '../entity';
import {BookService} from '../book.service';
import {ReaderService} from '../reader.service';
import {UserService} from '../user.service';

@Component({
  selector: 'app-update-reader-password',
  templateUrl: './update-reader-password.component.html',
  styleUrls: ['./update-reader-password.component.css']
})
export class UpdateReaderPasswordComponent implements OnInit {
  @Input() user: User;

  constructor(
    private route: ActivatedRoute,
    private bookService: BookService,
    private location: Location,
    private readerService: ReaderService,
    private userService: UserService
  ) {
  }

  ngOnInit() {
    this.readerService.getUser(this.userService.getReader().id)
      .subscribe(res => this.user = res);
  }

  goBack(): void {
    this.location.back();
  }

  save(password: string): void {// BUG!!!!!!!
    this.user = {id: this.userService.getReader().id, password: password.trim(), role: '读者'};
    this.readerService.changePassword(this.user)
      .subscribe((res) => {alert(res.message); this.goBack(); });
  }
}
