// service类用来处理数据，获取数据，与后台进行交互 ng g service school
import { Injectable } from '@angular/core';
// 实现Http通信(get/post/put/delete)
import { HttpClient, HttpHeaders } from '@angular/common/http';
// 监听来自后台的数据
import { Observable, of } from 'rxjs';
// 异常处理
import { catchError, map, tap } from 'rxjs/operators';
// 引入实体类，使用实体类中定义的属性
// tslint:disable-next-line:max-line-length
import {Book, Response} from './entity';
// 中间传输的选项数据（http传输，json格式）
const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};
// 注入依赖 （其他组件可调用）
@Injectable({
  providedIn: 'root'
})
export class BookService {
  private bookUrl = 'http://localhost:8080/book.ctl';
  // 接口，获取后端所提供的数据
  constructor(private http: HttpClient) { }
  // College类型的json数组（school组件调用）
  getBooks(): Observable<Book[]> {
    return this.http.get<Book[]>(this.bookUrl )
      .pipe(
        catchError(this.handleError<Book[]>('getBooks', []))
      );
  }
  deleteBook(book: Book | number): Observable<Response> {
    const id = typeof book === 'number' ? book : book.id;
    return this.http.delete<Response>(this.bookUrl + '?id=' + id, httpOptions).pipe(
      catchError(this.handleError<Response>('deleteBook'))
    );
  }
  /** POST: add a new college to the server */
  // tslint:disable-next-line:max-line-length
  addBook(book: { no: string; name: string; writer: string; press: string; address: string; total: string; borrowedNumber: string }): Observable<Response> {
    return this.http.post<Response>(this.bookUrl, book, httpOptions).pipe(
      catchError(this.handleError<any>('addBook'))
    );
  }
  /** GET college by id. Will 404 if id not found */
  getBook(id: number): Observable<Book> {
    const url = this.bookUrl + '?id=' + id;
    return this.http.get<Book>(url).pipe(
      catchError(this.handleError<Book>(`getBook id=${id}`))
    );
  }
  /** GET college by id. Will 404 if id not found */
  getBookByBookName(bookName: string): Observable<Book[]> {
    const url = this.bookUrl + '?bookName=' + bookName;
    return this.http.get<Book[]>(url).pipe(
      catchError(this.handleError<Book[]>(`getBook bookName=${bookName}`))
    );
  }
  /** GET college by id. Will 404 if id not found */
  getBookByWriter(writer: string): Observable<Book[]> {
    const url = this.bookUrl + '?writer=' + writer;
    return this.http.get<Book[]>(url).pipe(
      catchError(this.handleError<Book[]>(`getBook writer=${writer}`))
    );
  }
  /** GET college by id. Will 404 if id not found */
  getBookByPress(press: string): Observable<Book[]> {
    const url = this.bookUrl + '?press=' + press;
    return this.http.get<Book[]>(url).pipe(
      catchError(this.handleError<Book[]>(`getBook press=${press}`))
    );
  }
  /** GET college by id. Will 404 if id not found */
  getBooksByAddress(address: string): Observable<Book[]> {
    const url = this.bookUrl + '?address=' + address;
    return this.http.get<Book[]>(url).pipe(
      catchError(this.handleError<Book[]>(`getBook address=${address}`))
    );
  }
  updateBook(book: Book): Observable<Response> {
    return this.http.put<Response>(this.bookUrl, book, httpOptions).pipe(
      catchError(this.handleError<Response>('updateBook'))
    );
  }
  /**
   * Handle Http operation that failed.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.error(error); // log to console instead
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
