import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { BookComponent } from './book/book.component';
import { AddBookComponent } from './book/add-book.component';
import { UpdateBookComponent } from './book/update-book.component';
import { ReaderComponent } from './reader/reader.component';
import { AddReaderComponent } from './reader/add-reader.component';
import { UpdateReaderComponent } from './reader/update-reader.component';
// tslint:disable-next-line:import-spacing
import { LoginComponent } from  './login/login.component';
import { IndexComponent } from './index/index.component';
import {LogoutComponent} from './login/logout.component';
import {Index1Component} from './index1/index1.component';
import { BorrowingComponent } from './borrowing/borrowing.component';
import { BorrowBookComponent } from './borrowing/borrow-book.component';
import { ReturnBookComponent } from './return-book/return-book.component';
import { FindBorrowingComponent } from './find-borrowing/find-borrowing.component';
import { FindBookComponent } from './borrowing/find-book.component';
import { FindBookbyaddressComponent } from './book/find-bookbyaddress.component';
import { UpdateReaderPasswordComponent } from './update-reader-password/update-reader-password.component';
import { UpdateLibrarianPasswordComponent } from './update-librarian-password/update-librarian-password.component';
import { FindAllBorrowingComponent } from './find-all-borrowing/find-all-borrowing.component';
import { BorrowingManagementComponent } from './borrowing-management/borrowing-management.component';
import { FindBorrowingByReaderNameComponent } from './find-all-borrowing/find-borrowing-by-reader-name.component';
import { FindReaderComponent } from './reader/find-reader.component';
import { ReaderBorrowingComponent } from './reader-borrowing/reader-borrowing.component';

const routes: Routes = [
  {path: '', component: LoginComponent, pathMatch: 'full'}, // (空路径）默认路由(登录)
  {path: 'logout', component: LogoutComponent},
  {path: 'index', component: IndexComponent, children: [ // 嵌套路由
      { path: '', redirectTo: '/book', pathMatch: 'full' }, // 空路径 重定向
      { path: 'book', component: BookComponent},
      { path: 'addBook', component: AddBookComponent},
      { path: 'findBook/:bookName', component: FindBookbyaddressComponent},
      { path: 'updateBook/:id', component: UpdateBookComponent},
      { path: 'reader', component: ReaderComponent},
      { path: 'addReader', component: AddReaderComponent},
      { path: 'findReader/:readerName', component: FindReaderComponent},
      { path: 'updateReader/:id', component: UpdateReaderComponent},
      { path: 'findAllBorrowing', component: FindAllBorrowingComponent},
      { path: 'changePassword', component: UpdateLibrarianPasswordComponent},
      { path: 'borrowManagement', component: BorrowingManagementComponent},
      { path: 'readerBorrowManagement', component: ReaderBorrowingComponent},
      { path: 'findBorrowing/:readerName', component: FindBorrowingByReaderNameComponent}
      ]},
  {path: 'index1', component: Index1Component, children: [ // 嵌套路由
      { path: '', redirectTo: '/borrowing', pathMatch: 'full' }, // 空路径 重定向
      { path: 'borrowing', component: BorrowingComponent},
      { path: 'borrowBook/:id', component: BorrowBookComponent},
      { path: 'findBook/:style/:name', component: FindBookComponent},
      { path: 'findBorrowing', component: FindBorrowingComponent},
      { path: 'returnBook', component: ReturnBookComponent},
      { path: 'changePassword', component: UpdateReaderPasswordComponent}
    ]},
];
@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}
