import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { User1 } from '../entity';
import {BookService} from '../book.service';
import {UserService} from '../user.service';
import {LibrarianService} from '../librarian.service';
import {User1Service} from '../user1.service';

@Component({
  selector: 'app-update-librarian-password',
  templateUrl: './update-librarian-password.component.html',
  styleUrls: ['./update-librarian-password.component.css']
})
export class UpdateLibrarianPasswordComponent implements OnInit {
  @Input() user: User1;

  constructor(
    private route: ActivatedRoute,
    private bookService: BookService,
    private location: Location,
    private librarianService: LibrarianService,
    private user1Service: User1Service
  ) {
  }

  ngOnInit() {
    this.librarianService.getUser(this.user1Service.getLibrarian().id)
      .subscribe(res => this.user = res);
  }

  goBack(): void {
    this.location.back();
  }

  save(password: string): void {// BUG!!!!!!!
    this.user = {id: this.user1Service.getLibrarian().id, password: password.trim(), role: '管理员'};
    this.librarianService.changePassword(this.user)
      .subscribe((res) => {alert(res.message); this.goBack(); });
  }
}
