import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import {Book, Borrowing1, Reader1} from '../entity';
import {BookService} from '../book.service';
import {BorrowingService} from '../borrowing.service';
import {ReaderService} from '../reader.service';
import {UserService} from '../user.service';

@Component({
  selector: 'app-borrow-book',
  templateUrl: './borrow-book.component.html',
  styleUrls: ['./borrow-book.component.css']
})
export class BorrowBookComponent implements OnInit {
  @Input() book: Book;
  private reader: Reader1;
  private borrowing: Borrowing1;

  constructor(
    private route: ActivatedRoute,
    private bookService: BookService,
    private borrowingService: BorrowingService,
    private readerService: ReaderService,
    private userService: UserService,
    private location: Location
  ) {
  }

  ngOnInit() {
    const id = this.route.snapshot.params.id;
    this.bookService.getBook(id)
      .subscribe(res => this.book = res);
    this.readerService.getReader(this.userService.getReader().id)
      .subscribe(res => this.reader = res);
  }

  goBack(): void {
    this.location.back();
  }

  save(): void {
    this.borrowing = {id: null, book: this.book, reader: this.reader, date: null};
    this.borrowingService.borrowBook(this.borrowing)
      .subscribe((res) => { alert(res.message); this.goBack(); });
  }
}
