import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { Reader } from '../entity';
import {ReaderService} from '../reader.service';

@Component({
  selector: 'app-update-reader',
  templateUrl: './update-reader.component.html',
  styleUrls: ['./update-reader.component.css']
})
export class UpdateReaderComponent implements OnInit {
  @Input() reader: Reader;

  constructor(
    private route: ActivatedRoute,
    private readerService: ReaderService,
    private location: Location
  ) {
  }

  ngOnInit() {
    const id = this.route.snapshot.params.id;
    this.readerService.getReader(id)
      .subscribe(res => this.reader = res);
  }

  goBack(): void {
    this.location.back();
  }

  save(): void {
    this.readerService.updateReader(this.reader)
      .subscribe((res) => {alert(res.message); this.goBack(); });
  }
}
