import { Component, OnInit, Input} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import {ReaderService} from '../reader.service';
import {Reader} from '../entity';

@Component({
  selector: 'app-add-reader',
  templateUrl: './add-reader.component.html',
  styleUrls: ['./add-reader.component.css']
})
export class AddReaderComponent implements OnInit {
  @Input() reader: Reader;
  constructor(
    private route: ActivatedRoute,
    private readerService: ReaderService,
    private location: Location
  ) { }

  ngOnInit() {
  }
  // tslint:disable-next-line:variable-name
  save(no: string, name: string, sex: string, age: string, department: string, telNumber: string): void { // 多个文本框数据
    // tslint:disable-next-line:max-line-length
    this.reader = {id: null, no: no.trim(), name: name.trim(), age: age.trim(), sex: sex.trim(), department: department.trim(), telNumber: telNumber.trim()};
    // tslint:disable-next-line:max-line-length
    if (no.length === 0 || name.length === 0 || age.length === 0 || sex.length === 0 || department.length === 0 || telNumber.length === 0) {
      alert('请检查输入信息是否完整！');
    }
    if (!name) { return; }
    this.readerService.addReader(this.reader)
      .subscribe(() => this.goBack());
  }
  goBack(): void {
    this.location.back();
  }
}
